export default {
	data() {
		return {
			Invite_Code: '',
			Is_Check: false,
			User_Error: '',
			WXCode:'',
		}
	},
	methods: {
		check_code(e) {
			var code = e.detail.value;
			uni.request({
				url: '',
				method: 'POST',
				data: {
					wxcode:WXCode,
					code: code
				},
				success: res => {
					
					//邀请码是否正确
				}
			})
		},
		
		Submit(e) {

			if (this.Invite_Code != "1234") {
				this.User_Error = '请输入邀请码';
			} else {
				var Url = '/pages/backend/searchhome';
				uni.navigateTo({
					url: Url
				});
