		}
	},
	onLoad() {
		console.log('邀请码页面加载---------------------------------')
		uni.login({
			provider: 'weixin',
			success: function(res) {
				let currentwxcode = res.code;
				
				console.log('微信CODE'+currentwxcode)
				uni.request({
					url: '',
					method: 'POST',
					data: {
						code: currentwxcode,
					},
					success: res => {
						if(res.any){
							
							var Url = '/pages/backend/searchhome';
							uni.navigateTo({
								url: Url
							});
						}
					}
				})
			}
		})
					
	},
}
